.services {
  padding: 60px 0 45px;
  background-color: $lightgray;

  @include media(">=desktop") {
    padding: 100px 0 80px;
  }

  @include media(">=large-desktop") {
    padding: 140px 0 110px;
  }

  .h2 {
    margin-bottom: 25px;

    @include media(">=desktop") {
      margin-bottom: 40px;
      text-align: center;
    }

    @include media(">=large-desktop") {
      margin-bottom: 63px;
    }
  }

  .wrapp {

    @include media(">=tablet") {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -15px;
    }
  }
}