.whitepaper {
  padding-bottom: 10px;

  .container{
    position: relative;
  }

  .holder {
    position: relative;
    margin-top: -32px;
    padding: 52px 20px 50px;
    text-align: center;
    background-color: $lightgray;
    z-index: 5;
    overflow: hidden;
    border-radius: 2px 2px 0 0;

    @include media(">=desktop") {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: -52px;
      padding: 82px 200px 78px;
      text-align: left;
    }

    @include media(">=large-desktop") {
      margin-top: -102px;
      padding: 98px 260px 96px;
    }

    &.visible {

      &::before {
        transform: translate(-50%, 85%) scale(1);

        @include media(">=desktop") {
          transform: translate(-50%, 83%) scale(1);
        }

        @include media(">=large-desktop") {
          transform: translate(-50%, 85%) scale(1);
        }
      }
    }

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      border-radius: 50%;
      background-color: $darkgray;
      opacity: .075;
      transform-origin: center center;
      will-change: transform;
      transition: transform 2s ease-in-out!important;
      transform:  translate(-50%, 85%) scale(0);
      height: 294px;
      width: 294px;
      box-shadow:
          0 0 0 50px rgba($white, 1),
          0 0 0 105px rgba($darkgray, 0.5);

      @include media(">=desktop") {
        transform: translate(-50%, 83%) scale(0);
        height: 586px;
        width: 586px;
        box-shadow:
            0 0 0 95px rgba($white, 1),
            0 0 0 205px rgba($darkgray, 0.5);
      }

      @include media(">=large-desktop") {
        transform: translate(-50%, 85%) scale(0);
        height: 820px;
        width: 820px;
        box-shadow:
            0 0 0 130px rgba($white, 1),
            0 0 0 280px rgba($darkgray, 0.5);
      }
    }

    &::after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 4px;
      border-radius: 2px;
      background-color: $green;
    }

    & > div {

      @include media(">=desktop") {
        margin-left: 130px;
      }

      @include media(">=large-desktop") {
        margin-left: 163px;
        margin-top: 6px;
      }
    }
  }

  .image {
    position: relative;
    display: block;
    width: 190px;
    height: auto;
    margin: 0 auto 32px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    z-index: 5;

    @include media(">=desktop") {
      margin: 0;
      min-width: 270px;
      width: 270px;
    }

    @include media(">=large-desktop") {
      min-width: 360px;
      width: 360px;
    }

    img {
      display: block;
      max-height: 100%;
      height: auto;
      width: 100%;
    }
  }

  .h4 {
    margin-bottom: 42px;

    @include media(">=desktop") {
      margin-bottom: 20px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 27px;
    }
  }

  .description {
    display: none;

    @include media(">=desktop") {
      display: block;
      margin-bottom: 40px;
    }
  }
}