.page-poster {
  padding: 45px 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  @include media(">=desktop") {
    padding: 60px 0;
  }

  @include media(">=large-desktop") {
    min-height: 606px;
    padding: 97px 0;
  }

  &.team-poster {
    min-height: 480px;
    margin-bottom: 60px;
    padding: 173px 15px 30px;

    @include media(">=desktop") {
      padding: 242px 90px 60px;
      min-height: 600px;
      margin-bottom: 100px;
    }

    @include media(">=large-desktop") {
      min-height: 800px;
      padding: 344px 130px 97px;
      margin-bottom: 140px;
    }

    .button {
      white-space: pre-wrap;
    }
  }

  .description {
    padding: 30px;
    background-color: $white;
    background-image: url(#{$images-path}bottom-bg-dots.png);
    background-repeat: no-repeat;
    background-position: 120% bottom;
    position: relative;
    max-width: 520px;
    margin: 0 auto;

    @include media(">=desktop") {
      padding: 50px;
      margin: 0;
      max-width: 620px;
      margin-left: auto;

      &:hover{
        &::after{
          left: 0;
          right: auto;
          width: 100%;
        }
      }
    }

    @include media(">=large-desktop") {
      padding: 70px;
    }

    &::after{
      content: '';
      position: absolute;
      bottom: -2px;
      left: auto;
      right: 0;
      height: 4px;
      border-radius: 2px;
      width: 0;
      background-color: $green;
      transition: width 0.3s;
    }

    p {
      color: $bodygray;

      &:last-of-type {
        margin-bottom: 20px;

        @include media(">=desktop") {
          margin-bottom: 30px;
        }

        @include media(">=large-desktop") {
          margin-bottom: 40px;
        }
      }
    }
  }

  .h4 {
    margin-bottom: 15px;

    @include media(">=desktop") {
      margin-bottom: 20px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 27px;
    }
  }
}