.small-description{
  text-align: center;
  max-width: 1012px;
  margin: 0 auto;
  padding: 20px 0;
  color: $bodygray;

  @include media(">=tablet"){
    padding: 35px 0;
  }

  @include media(">=desktop"){
    padding-top: 75px;
  }

  @include media(">=large-desktop"){
    padding-top: 100px;
  }


  .content-holder & {

    @include media(">=desktop"){
      padding-top: 50px;
    }

    @include media(">=large-desktop"){
      padding-top: 60px;
    }
  }


  p{

    &+p{
      margin-top: 30px;
    }

  }

  .button {
    margin-top: 25px;

    @include media(">=tablet"){
      margin-top: 35px;
    }

    @include media(">=desktop"){
      margin-top: 41px;
    }
  }

  .button.linkedin + h2,
  .button.linkedin + .h2 {
    margin: 40px 0 15px;

    @include media(">=desktop"){
      margin: 70px 0 20px;
    }

    @include media(">=large-desktop"){
      margin: 108px 0 23px;
    }
  }

  &.data-center-details {
    margin-bottom: 15px;

    @include media(">=desktop"){
      margin-bottom: 35px;
    }

    @include media(">=large-desktop"){
      margin-bottom: 65px;
    }
  }
}

