.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  z-index: 17;
  background-color: $white;
  transition: opacity 0s;

  @include media(">=desktop") {
    padding: 12px 55px 0;
    align-items: flex-start;
    justify-content: flex-start;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);

    @supports (padding-top: 1px) {
      position: fixed;
    }
  }

  @include media(">=large-desktop") {
    padding: 25px 50px 0;
  }

  .home & {
    background-color: transparent;
    box-shadow: none;
  }

  &.sticky {
    position: fixed;
    background-color: $white;

    @include media(">=desktop") {
      box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
    }

    .white-logo {
      display: none !important;
    }

    .gray-logo {
      display: block !important
    }

    .burger {

      &::before,
      &::after {
        background-color: $black !important;
      }
    }
  }

  .menu-opened & {
    position: fixed;

    @include media("<desktop") {
      background-color: $white;
    }

    .white-logo {
      display: none !important;
    }

    .gray-logo {
      display: block !important
    }

    .burger {

      &::before,
      &::after {
        background-color: $black !important;
      }
    }

  }

  &.hide {
    opacity: 0;

    @include media(">=desktop") {
      .main-nav {
        pointer-events: none;
      }
    }

    .burger, .main-logo {
      pointer-events: none;
    }
  }

  .main-logo {
    position: relative;
    display: inline-block;
    height: 40px;
    width: 180px;
    z-index: 15;
    margin-right: auto;

    @include media(">=desktop") {
      height: 50px;
      width: 19.481%;
      max-width: 225px;
      margin-top: 20px;
      margin-right: 10px;
    }

    @include media(">=large-desktop") {
      height: 60px;
      max-width: none;
      width: 270px;
      margin-top: 10px;
    }

    img {
      max-height: 100%;
      height: auto;
      width: 100%;
    }
  }

  .white-logo {
    display: none;

    .menu-opened & {
      display: none;
    }

    .home & {
      display: block;
    }
  }

  .gray-logo {

    .home & {
      display: none;
    }

    .menu-opened & {
      display: block;
    }

    .home.menu-opened & {
      display: none;
    }
  }

  .burger {
    position: relative;
    height: 16px;
    width: 30px;
    background-color: transparent;
    border: none;
    background-repeat: no-repeat;
    z-index: 15;
    margin-left: auto;

    @include media(">=desktop") {
      display: none;
    }

    &::before,
    &::after {
      position: absolute;
      content: '';
      left: 0;
      height: 3px;
      width: 100%;
      border-radius: 2px;
      background-color: $black;

      .home & {
        background-color: $white;
      }

      .menu-opened & {
        background-color: $black;
      }

      .menu-opened.home & {
        background-color: $white;
      }
    }

    &::before {
      top: 0;

      .menu-opened & {
        transform: translate(0, 6.5px) rotate(45deg);
      }
    }

    &::after {
      bottom: 0;

      .menu-opened & {
        transform: translate(0, -6.5px) rotate(-45deg);
      }
    }
  }
}

.main-nav {
  position: absolute;
  top: 70px;
  left: 0;
  height: calc(100vh - 70px);
  width: 100%;
  padding: 28px 0 120px 40px;
  z-index: 15;
  transition: .3s;
  opacity: 0;
  pointer-events: none;

  @include media(">=375px") {
    padding-bottom: 180px;
  }

  @include media(">=desktop") {
    position: static;
    display: block;
    top: auto;
    left: auto;
    height: auto;
    width: auto;
    max-width: 100%;
    min-width: 1%;
    margin-left: auto;
    padding: 40px 0 0 0;
    opacity: 1;
    pointer-events: auto;
    z-index: 1;
  }

  @include media(">=large-desktop") {
    padding: 31px 0 0 0;
  }

  .menu-opened & {
    background-color: $darkgray;
    opacity: 1;
    pointer-events: auto;

    @include media(">=desktop") {
      background-color: transparent;
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: calc(100% - 90px);
    width: 100%;
    background-color: $white;
    pointer-events: none;

    @include media(">=desktop") {
      display: none;
    }

    .menu-opened & {
      box-shadow: inset 0 40px 40px -40px rgba(0, 0, 0, 0.1);
    }
  }

  &::after {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 120px;
    background: linear-gradient(/*bg-color*/
                    $darkgray (6px - 2px), transparent 1%) center,
    linear-gradient(90deg, /*bg-color*/
                    $darkgray (6px - 2px), transparent 1%) center,
      /*dot-color*/
    $pattern-dots-color;
    background-size: 6px 6px;
    transform-style: preserve-3d;
    pointer-events: none;

    @include media(">=375px") {
      height: 180px;
    }

    @include media(">=desktop") {
      display: none;
    }
  }

  .holder {
    overflow-y: auto;
    height: calc(100% - 4px);
    padding-bottom: 10px;

    @include media(">=desktop") {
      display: flex;
      align-items: baseline;
      height: auto;
      overflow: visible;
      padding-bottom: 0;
    }
  }

  .button {
    position: absolute;
    left: 50%;
    bottom: 60px;
    transform: translateX(-50%);
    z-index: 10;
    transform-style: preserve-3d;

    &::before {
      color: $white;
    }

    @include media(">=375px") {
      bottom: 120px;
    }

    @include media(">=desktop") {
      position: relative;
      left: auto;
      bottom: auto;
      transform: none;

      &::before {
        color: $black;
      }

      .sticky & {

        &::before {
          color: $black;
        }

        &::after {
          color: $white;
        }

        .home & {

          &::before {
            color: $black;
          }
        }
      }
    }

    .home & {

      &::before,
      &::after {
        color: $white;
      }
    }
  }

  .main-menu {
    margin-bottom: 23px;

    @include media(">=desktop") {
      display: flex;
      margin-bottom: 0;
    }

    & > li {
      margin-bottom: 14px;
      padding-right: 40px;

      &.active, &.current-menu-parent {

        & > a {

          &::before {

            @include media(">=desktop") {
              transform: scaleX(1);
            }
          }
        }
      }

      @include media(">=desktop") {
        margin: 0 20px 0 0;
        padding: 0 0 32px 0;
        display: flex;
        flex-direction: column;
        z-index: 1;
        min-width: 4%;
        &:hover {

          & > div {
            visibility: visible;
            opacity: 1;
            will-change: opacity;
          }

          .ul {
            display: block;
          }

          & > a::before {
            transform-origin: bottom left;
            transform: scaleX(1);
          }

          .sublist {
            visibility: visible;
            opacity: 1;
          }
        }
      }

      @include media(">=1280px") {
        margin-right: 30px;
      }

      @include media(">=large-desktop") {
        margin: 0 50px 0 0;
      }

      &:last-of-type {
        margin-bottom: 0;
      }

      &.opened {

        .sublist {
          display: block;

          @include media(">=desktop") {
            display: flex;
          }
        }

        & > a::before {
          @include media("<desktop") {
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;
            border-top: none;
            border-bottom: 6px solid $green;
          }
        }
      }

      & > a {
        position: relative;
        display: inline-block;
        padding: 0 18px 0 0;
        border: none;
        background-color: transparent;
        color: $black;
        font-size: 16px;
        line-height: 20px;
        font-weight: $semibold;
        text-decoration: none;

        @include media(">=desktop") {
          padding: 0;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-grow: 1;
          .home & {
            color: $white;
          }

          .sticky & {
            color: $black;
          }
        }

        @include media(">=large-desktop") {
          font-size: 18px;
          line-height: 22px;
        }

        &::before {
          position: absolute;
          content: '';
          top: 50%;
          right: 0;
          transform: translateY(-50%);
          height: 7px;
          width: 10px;
          border-left: 5px solid transparent;
          border-right: 5px solid transparent;
          border-top: 6px solid $green;

          @include media(">=desktop") {
            top: auto;
            bottom: -32px;
            left: 0;
            height: 4px;
            width: 100%;
            border-radius: 2px;
            border: none;
            transform: scaleX(0);
            transform-origin: bottom right;
            background-color: $green;
            transition: transform .3s;
            z-index: 10;
            will-change: transform;
            pointer-events: none;
          }
        }

        & + div {
          position: static;

          @include media(">=desktop") {
            position: absolute;
            visibility: hidden;
            top: 100%;
            left: 0;
            width: 100%;
            padding: 0 160px;
            opacity: 0;
            background-color: $lightgray;
            transition: opacity .3s;
            will-change: opacity;

            &::before {
              content: '';
              position: absolute;
              top: 0;
              left: 50%;
              transform: translateX(-50%);
              height: 100%;
              width: 200vw;
              box-shadow: inset 0 30px 40px rgba(0, 0, 0, 0.1);
              pointer-events: none;
            }
          }

          @include media(">=large-desktop") {
            padding: 0 325px;
          }
        }
      }
    }
  }

  .services-solutions {

    .sublist {

      @include media(">=desktop") {
        position: static;
        display: flex;
        flex-direction: row;
        max-width: 1270px;
        margin: 0 auto;

        &::before {
          display: none;
        }
      }

      & > li:first-child {

        @include media(">=desktop") {
          margin: 0;
          margin-right: 10px;
        }

        & > ul {

          @include media(">=desktop") {
            display: flex;
          }

          & > li {

            @include media(">=desktop") {
              margin: 0 35px 0 0;
            }

            @include media(">=large-desktop") {
              margin: 0 115px 0 0;
            }
          }
        }
      }

      & > li:nth-child(2) {
        margin-left: auto;
      }

      & > li {

        & > a {
          color: $green;

          @include media(">=desktop") {
            position: relative;
            padding-right: 20px;
            font-size: 18px;
            line-height: 22px;

            &:hover {
              color: $darkgreen;
              text-decoration: none;
            }

            &::before {
              position: absolute;
              content: '';
              top: 50%;
              right: 0;
              transform: translateY(-45%);
              height: 6px;
              width: 9px;
              border-left: 6px solid $green;
              border-top: 5px solid transparent;
              border-bottom: 5px solid transparent;
            }
          }
        }
      }

      ul {
        padding-top: 13px;

        @include media(">=desktop") {
          padding-top: 28px;
        }

        ul {
          padding: 13px 0 0 24px;

          @include media(">=desktop") {
            padding: 20px 0 0 0;
          }

          li {
            margin-bottom: 13px;

            &:last-of-type {

              @include media(">=desktop") {
                margin-bottom: 0;
              }
            }
          }

          a {
            color: $graphitegray;

            @include media(">=desktop") {
              font-size: 16px;
            }
          }
        }
      }
    }

  }

  .sublist {
    display: none;
    padding: 13px 0 0 24px;

    @include media(">=desktop") {
      position: absolute;
      display: block;
      flex-direction: column;
      visibility: hidden;
      top: 100%;
      opacity: 0;
      transition: opacity .3s;
      will-change: opacity;
      padding: 30px 0 40px 0;
    }

    &::before {

      @include media(">=desktop") {
        position: absolute;
        content: '';
        top: 0;
        left: -100vw;
        height: 100%;
        width: 200vw;
        background-color: $lightgray;
        box-shadow: inset 0 30px 40px rgba(0, 0, 0, 0.1);
      }
    }

    li {
      margin-bottom: 18px;

      @include media(">=desktop") {
        margin-bottom: 15px;
        padding: 0;
      }

      &:last-of-type {
        margin-bottom: 0;
      }
    }

    a {
      position: relative;
      color: $irongray;
      font-size: 16px;
      font-weight: 600;
      line-height: 20px;
      text-decoration: none;

      @include media(">=desktop") {
        font-size: 18px;
        font-weight: 600;
        line-height: 22px;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  .info {
    position: relative;
    z-index: 10;

    @include media(">=desktop") {
      position: absolute;
      top: 16px;
      right: 70px;
      display: flex;
      align-items: flex-start;
      z-index: 2;
    }

    @include media(">=large-desktop") {
      top: 20px;
      right: 64px;
    }

    ul {
      display: flex;
      margin-bottom: 9px;

      @include media(">=desktop") {
        margin: 0;
      }
    }

    li {
      position: relative;
      margin-right: 15px;
      padding-right: 15px;

      @include media(">=desktop") {
        margin-right: 20px;
        padding-right: 22px;
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(50%, 0);
        height: 20px;
        width: 1px;
        background-color: $silvergray;
      }

      &:last-child {
        margin: 0;
        padding: 0;

        &::before {
          display: none;
        }
      }
    }

    a {
      margin-right: 13px;
      padding-right: 17px;
      font-size: 14px;
      font-weight: 600;
      line-height: 18px;
      color: $trafficgray;
      text-decoration: none;
      border-right: 1px solid $silvergray;

      &:hover {
        text-decoration: underline;
      }

      @include media(">=desktop") {
        vertical-align: top;
        color: $trafficgray;

        .home & {
          color: $silvergray;
        }

        .sticky & {
          color: $trafficgray;
        }
      }

      &:last-of-type {
        margin-right: 0;
        padding-right: 0;
        border: none;
      }

      &.phone-link {
        color: $irongray;
        font-size: 16px;
        padding-left: 26px;

        &:hover {
          text-decoration: none;
        }

        @include media(">=desktop") {
          padding-left: 24px;
          margin-left: 43px;

          .home & {
            color: $cloudgray;
          }

          .sticky & {
            color: $irongray;

            svg path {
              fill: $irongray;
            }
          }
        }

        @include media(">=large-desktop") {
          margin-left: 64px;
        }
      }
    }
  }
}