.banner {
  position: relative;

  &::before,
  &::after {
    content: '';
    position: absolute;
    left: 0;
    height: 100px;
    width: 100%;
    background-image: linear-gradient(180deg, rgba(24, 29, 33, 0) 0%, #181d21 100%);
    pointer-events: none;
    z-index: 5;

    @include media(">=desktop") {
      height: 200px;
    }
  }

  &::before {
    top: 0;
    transform: rotate(180deg);
  }

  &::after {
    bottom: 0;
  }

  .slide {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 80% top;

    @include media(">=desktop") {
      background-position: center top;
    }
  }

  .container {
    position: relative;
  }

  .slide-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 85vh;
    padding: 90px 0 110px;

    @include media(">=tablet"){
      min-height: 700px;
    }

    @include media(">=desktop") {
      max-width: 800px;
      padding: 135px 0 180px;
      background-position: center top;
      min-height: 100vh;
    }

    @include media(">=large-desktop") {
      max-width: 1140px;
      padding: 155px 0 195px;
    }

    @media (-ms-high-contrast: none), (-ms-high-contrast: active) {
      padding-top: 20%;
    }
  }

  .title {
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: $bold;
    font-family: $nunito;
    color: $white;
    font-size: 32px;
    line-height: 38px;

    @include media(">=desktop") {
      margin-bottom: 28px;
      padding-left: 5px;
      font-size: 60px;
      line-height: 70px;
    }

    @include media(">=large-desktop") {
      font-size: 80px;
      line-height: 86px;
    }

    & + p {
      color: $green;
      font-size: 20px;
      font-weight: 600;
      line-height: 26px;
      margin-bottom: 20px;

      @include media(">=desktop") {
        padding-left: 5px;
        font-size: 36px;
        line-height: 42px;
      }
    }
  }

  .link-explore {
    position: absolute;
    bottom: 4.5vh;
    padding-right: 45px;
    color: $white;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    text-decoration: none;
    z-index: 6;

    @include media(">=desktop") {
      bottom: 85px;
      padding-right: 60px;
      font-size: 18px;
      line-height: 22px;
    }

    @include media(">=large-desktop") {
      bottom: 110px;
    }

    span {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      display: block;
      height: 51px;
      width: 46px;
      z-index: 1;

      @include media(">=desktop") {
        height: 70px;
        width: 63px;
      }

      svg {
        position: absolute;
        max-height: 100%;
        height: auto;
        width: 100%;
      }
    }

    .border{
      fill: none;
      stroke: $green;
      stroke-width: 4;
      stroke-dasharray: 200 200;
      stroke-dashoffset: -140;
    }


    &:hover {

      //svg path:first-child{
      //  stroke: #585c5e !important;
      //  stroke-width: 2 !important;
      //}

      .border {
        stroke-dashoffset: 20!important;
        //stroke: $green;
        transition: .35s ease;
      }
    }


  }
}