.author-description{
  position: relative;
  width: 100%;
  background-color: $lightgray;
  padding: 37px 15px 10px;
  margin-top: 30px;

  @include media(">=mobile-landscape"){
    padding-left: 30px;
    padding-right: 30px;
    margin-top: 40px;
  }

  @include media(">=tablet"){
    padding: 6.2% 6.2% 3%;
    margin-top: 70px;
  }

  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 4px;
    height: 100%;
    border-radius: 2px;
    background-color: $green;
  }

  & + *{
    margin-top: 30px;

    @include media(">=mobile-landscape"){
      margin-top: 40px;
    }

    @include media(">=tablet"){
      margin-top: 70px;
    }
  }

  .heading{
    text-align: left;
    padding-top: 0;
    margin-bottom: 18px;
  }
}