.error-page {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 0;
  text-align: center;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;

  @include media(">=desktop") {
    padding: 120px 0;
  }

  @include media(">=large-desktop") {
    padding: 130px 0;
  }

  @media (-ms-high-contrast: none), (-ms-high-contrast: active) {
    padding-top: 20%;
  }

  .main-logo {
    position: absolute;
    top: 15px;
    left: 15px;
    display: inline-block;
    height: 40px;
    width: 180px;
    z-index: 15;

    @include media(">=desktop") {
      top: 15px;
      left: 50px;
      height: 50px;
      width: 225px;
    }

    @include media(">=large-desktop") {
      top: 32px;
      left: 50px;
      height: 60px;
      width: 270px;
    }

    img {
      max-height: 100%;
      height: auto;
      width: 100%;
    }
  }

  .white-logo {
    display: none;
  }

  .wrapp {
    padding: 0 15px;

    @include media(">=desktop") {
      padding: 0 30px;
    }
  }

  .title {
    color: $titlegray;
    font-family: $nunito;
    font-size: 60px;
    font-weight: $semibold;
    line-height: 64px;

    @include media(">=desktop") {
      font-size: 120px;
      line-height: 124px;
    }

    & + div {
      margin-bottom: 20px;
      color: $bodygray;
      font-size: 22px;
      line-height: 28px;

      @include media(">=desktop") {
        margin-bottom: 41px;
        font-size: 28px;
        line-height: 38px;
      }
    }
  }
}