.connection {
  background-color: $lightgray;
  padding: 60px 15px;
  text-align: center;
  @include media(">=desktop") {
    padding: 80px 30px;
  }

  @include media(">=large-desktop") {
    padding-top: 140px;
    padding-bottom: 140px;
  }

  .wrapp {
    max-width: 750px;
    margin: 0 auto;
  }

  .h2 {
    margin-bottom: 30px;

    & + p {
      margin-bottom: 30px;
    }
  }

  form {
    padding-top: 30px;
    text-align: left;
    @include media(">=tablet") {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      padding-top: 40px;
    }

    .green {
      color: $green;
    }
  }

  .form-group {
    margin-bottom: 15px;
    @include media(">=tablet") {
      width: 360px;
      margin-bottom: 30px;
    }
  }

  .controls,
  .message {
    width: 100%;
  }

  .message {
    margin-bottom: 25px;

    @include media(">=tablet") {
      margin-bottom: 34px;
    }
  }

  .controls {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    margin: 0 -5px;

    @include media(">=tablet") {
      margin: 0;
      justify-content: flex-end;
    }
  }

  .capcha,
  button {
    margin: 5px;

    @include media(">=tablet") {
      margin: 0;
    }
  }

  .capcha {
    margin-right: auto;
    img {
      display: block;
    }
  }

}