.video-wrapper {
  position: relative;
  width: 100%;
  padding-top: 56.25%;
  margin: 25px auto;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  @include media(">=tablet") {
    padding-top: 0;
    margin: 35px auto;
    height: 422px;
    max-width: 750px;
  }

  @include media(">=desktop") {
    margin: 49px auto 51px;
  }

  video{
    opacity: 0;
  }

  &.active {
    background-image: none !important;
    .play-btn {
      opacity: 0;
      pointer-events: none;
    }
    video {
      opacity: 1;
    }
  }

  video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    @include media(">=tablet"){
      position: static;
    }
  }

  .play-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
  }
}