.checkmark-list {
  margin-bottom: 45px;
  text-align: left;

  @include media(">=desktop") {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
  }

  @include media(">=large-desktop") {
    margin-bottom: 76px;
  }

  .advantages & {
    margin: 0;
    padding: 20px;
    border: 2px solid $cloudgray;

    @include media(">=desktop"){
      justify-content: flex-start;
      padding: 20px;
    }

    @include media(">=large-desktop"){
      padding: 50px;
    }
  }

  .makes-different & {

    @include media(">=tablet"){
      text-align: left;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      margin: 0 -15px 15px;
      max-width: 1170px;
    }

    @include media(">=desktop"){
      width: 83%;
      margin-bottom: -30px;
    }
  }

  .data-center-options & {
    @include media(">=desktop"){
      margin: 0;
    }
  }

  li {
    position: relative;
    margin-bottom: 20px;
    padding: 10px 0 0 65px;
    color: $cloudgray;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    background-image: url(#{$images-path}checkmark.svg);
    background-position: left top;
    background-repeat: no-repeat;
    background-size: 52px 50px;

    .advantages &,
    .makes-different &,
    .data-center-options & {
      background-image: url(#{$images-path}checkmark-white.svg);
    }

    .advantages & {
      color: $bodygray;

      @include media(">=desktop") {
        max-width: 440px;
        width: calc(33.33% - 20px);
        margin-right: 20px;

        &:last-child{
          margin-right: 0;
        }
      }

      @include media(">=large-desktop") {
        width: 440px;
        margin-right: 49px;
      }
    }

    .data-center-options & {
      color: $bodygray;

      @include media(">=desktop") {
        margin-right: 20px;
        max-width: 300px;
        padding: 20px 0 0 80px;
      }

      @include media(">=large-desktop") {
        margin-right: 95px;
        max-width: 280px;
        padding: 20px 0 0 95px;
      }
    }

    .makes-different & {
      color: $bodygray;

      @include media(">=tablet"){
        width: calc(33.33% - 30px);
        margin: 0 15px 30px;
      }

      @include media(">=desktop") {
        padding: 20px 0 0 80px;
      }

      @include media(">=large-desktop") {
        padding: 20px 0 0 95px;
      }
    }

    @include media(">=desktop") {
      max-width: 360px;
      margin: 0;
      padding: 20px 0 0 80px;
      font-size: 18px;
      line-height: 28px;
      background-size: 72px 70px;
    }

    @include media(">=large-desktop") {
      max-width: 435px;
      margin: 0 50px 0 0;
      padding: 20px 0 0 100px;
    }

    &:last-of-type {
      margin-bottom: 0;
    }

    p {
      margin-bottom: 6px;
      color: $white;
      font-size: 24px;
      font-weight: 600;
      line-height: 30px;

      .data-center-options &,
      .makes-different &,
      .advantages & {
        color: $titlegray;
      }

      @include media(">=desktop") {
        font-size: 30px;
        line-height: 36px;
      }
    }
  }
}