.contacts-map {
  padding: 40px 0;

  @include media(">=tablet") {
    padding: 65px 0;
  }

  @include media(">=desktop") {
    padding: 80px 0;
    display: flex;
    align-items: center;
  }

  @include media(">=large-desktop") {
    padding: 100px 0;
  }

  .map-block {
    position: relative;
    width: 100%;
    min-height: 290px;
    max-width: 650px;
    margin: 0 auto 25px;
    background-color: $bodygray;
    
    @include media(">=desktop"){
      width: auto;
      max-width: 880px;
      min-width: 57.5%;
      margin: 0;
      margin-right: 5%;
    }

    @include media(">=large-desktop"){
      margin-right: 10.45%;
    }
    
    .ratio {
      width: 100%;
      height: 0;
      background-color: transparent;
      pointer-events: none;
      padding-top: 61.35%;
    }
    
  }

  .text{
    margin-top: 12px;
    flex-grow: 1;
    .h4{
      margin-bottom: 25px;
    }

    address{
      font-style: normal;
      font-weight: 600;
      color: $titlegray;
      margin-bottom: 8px;
    }

    p{
      margin-bottom: 3px;
      color: $bodygray;
    }


    .right-link{
      margin-bottom: 28px;
      display: inline-block;
      span{
        font-size: 18px;
        line-height: 22px;
        &::before,
        &::after{
          color: $darkgreen;
        }
      }
    }
  }
}