.team {
  margin-bottom: 40px;

  @include media(">=mobile-landscape") {
    display: flex;
    flex-wrap: wrap;
  }

  @include media(">=desktop") {
    margin-bottom: 60px;
  }

  @include media(">=large-desktop") {
    margin-bottom: 75px;
  }

  .person-card {
    margin-bottom: 30px;

    @include media(">=mobile-landscape") {
      width: calc(50% - 15px);
      margin: 0 30px 45px 0;

      &:nth-of-type(2n) {
        margin-right: 0;
      }
    }

    @include media(">=desktop") {
      width: calc(33.33% - 20px);
      margin: 0 30px 45px 0;

      &:nth-of-type(2n) {
        margin-right: 30px
      }

      &:nth-of-type(3n) {
        margin-right: 0;
      }
    }

    @include media(">=large-desktop") {
      margin: 0 30px 68px 0;
    }

    a {
      text-decoration: none;
    }

    .photo {
      position: relative;
      margin-bottom: 15px;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center top;


      &:last-of-type {
        margin-bottom: 0;
      }

      @include media(">=desktop") {
        margin-bottom: 30px;
        transition: box-shadow .3s;
        box-shadow:  0 0 0 rgba(0, 0, 0, 0.1), 0 0 0 rgba(0, 0, 0, 0.1);

        &:hover {
          box-shadow:  0 0 40px rgba(0, 0, 0, 0.1), 0 20px 40px rgba(0, 0, 0, 0.1);

          &::after {
            transform: scaleX(1);
            transition-delay: 0s;
            transform-origin: left;
          }
        }
      }

      &::before {
        content: '';
        display: block;
        padding-bottom: 100%;
      }

      &::after {
        content: '';
        position: absolute;
        bottom: -2px;
        left: 0;
        height: 4px;
        width: 100%;
        transform: scaleX(0);
        border-radius: 2px;
        background-color: $green;
        transition: transform .3s;
        transform-origin: right;
      }

      a {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        height: 100%;
        width: 100%;
      }
    }

    .h4 {
      margin-bottom: 5px;

      @include media(">=desktop") {
        margin-bottom: 7px;
      }
    }

    .position {
      color: $graphitegray;
    }
  }
}