.specs {
  position: relative;
  margin-bottom: 70px;
  padding: 40px 20px;
  background-color: $lightgray;
  overflow: hidden;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 4px;
    width: 100%;
    border-radius: 2px;
    background-color: $green;
  }

  &.visible {

    &::before {
      transform: translate(85%, -55%) scale(1);

      @include media(">=desktop") {
        transform: translate(85%, -55%) scale(1);
      }

      @include media(">=large-desktop") {
        transform: translate(85%, -55%) scale(1);
      }
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: 326px;
    right: 0;
    border-radius: 50%;
    background-color: $lightgray;
    opacity: .075;
    transform-origin: right bottom;
    will-change: transform;
    transition: transform 2s ease-in-out!important;
    transform: translate(85%, -55%) scale(0);
    height: 294px;
    width: 294px;
    box-shadow:
        0 0 0 90px rgba($darkgray, .5),
        0 0 0 160px rgba($lightgray, 1),
        0 0 0 250px rgba($darkgray, .4);

    @include media(">=desktop") {
      transform: translate(85%, -55%) scale(0);
      height: 400px;
      width: 400px;
      box-shadow:
          0 0 0 120px rgba($darkgray, .5),
          0 0 0 220px rgba($lightgray, 1),
          0 0 0 340px rgba($darkgray, .4);
    }

    @include media(">=large-desktop") {
      top: 368px;
      transform: translate(85%, -55%) scale(0);
      height: 500px;
      width: 500px;
      box-shadow:
          0 0 0 160px rgba($darkgray, .5),
          0 0 0 290px rgba($lightgray, 1),
          0 0 0 450px rgba($darkgray, .4);
    }
  }

  @include media(">=desktop") {
    margin-bottom: 100px;
    padding: 70px 50px 35px;
  }

  @include media(">=large-desktop") {
    margin-bottom: 140px;
    padding: 104px 120px 40px;
  }

  .heading {
    margin-bottom: 20px;

    @include media(">=desktop") {
      display: flex;
      justify-content: space-between;
      align-items: baseline;
      margin-bottom: 25px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 29px;
    }
  }

  .wrapp {

    @include media(">=desktop") {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }

  .item {
    position: relative;
    margin-bottom: 25px;

    @include media(">=desktop") {
      width: 100%;
      max-width: calc(50% - 30px);
      margin-bottom: 30px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 36px;
    }

    &.active {
      .h6 span::after {
        transform: translate(-50%, -50%) rotate(0deg);
      }

      ul {
        display: flex;
      }
    }
  }

  .h6 {
    position: relative;
    padding: 0 40px 15px 0;
    border-bottom: 2px solid $cloudgray;
    cursor: pointer;

    @include media(">=desktop") {
      padding-bottom: 18px;
    }

    @include media(">=large-desktop") {
      padding-bottom: 20px;
    }

    span {
      position: absolute;
      top: 0;
      right: 0;
      height: 30px;
      width: 30px;
      border-radius: 50%;
      border: 2px solid $silvergray;

      &::before,
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 2px;
        width: 12px;
        background-color: $green;
      }

      &::after {
        transform: translate(-50%, -50%) rotate(90deg);
        transition: transform .3s;
      }
    }
  }

  ul {
    position: relative;
    display: none;
    flex-direction: column;

    @include media(">=desktop") {
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }

  li {
    position: relative;
    margin: 0 0 6px 0;
    padding-left: 20px;
    color: $bodygray;
    font-size: 16px;
    line-height: 26px;

    &:last-of-type {
      margin: 0;
    }

    &::before {
      content: '';
      position: absolute;
      top: 8px;
      left: 6px;
      height: 8px;
      width: 8px;
      border-radius: 50%;
      background-color: $green;
    }

    @include media(">=desktop") {
      width: 100%;
      max-width: calc(50% - 25px);
      margin: 0 20px 8px 0;
      font-size: 18px;
      line-height: 28px;

      &:nth-of-type(2n) {
        margin: 0 0px 8px 0;
      }
    }

    @include media(">=large-desktop") {
      margin-bottom: 10px;
      padding-left: 30px;
    }
  }
}