.investors-block {
  padding-bottom: 25px;

  @include media(">=tablet") {
    padding-bottom: 40px;
  }

  @include media(">=desktop") {
    padding-bottom: 70px;
  }

  @include media(">=large-desktop") {
    padding-bottom: 96px;
  }

  .item {
    position: relative;
    max-width: 1270px;
    padding-bottom: 35px;
    margin: 0 auto 35px;

    @include media(">=tablet") {
      display: flex;
      align-items: flex-start;
    }

    @include media(">=desktop") {
      padding-bottom: 50px;
      margin-bottom: 50px;
    }

    @include media(">=large-desktop") {
      padding-bottom: 70px;
      margin-bottom: 74px;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      height: 2px;
      width: 100%;
      background-color: $cloudgray;
    }

    &:last-child {
      margin-bottom: 0;
      &::after {
        display: none;
      }
    }
  }

  .image {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 400px;
    height: 100px;
    margin: 0 auto 25px;
    text-align: center;

    @include media(">=tablet") {
      min-width: 32%;
      margin: 0;
      margin-right: 8.81%;
      height: 120px;
    }

    @include media(">=desktop") {
      height: 145px;
    }

    @include media(">=large-desktop") {
      height: 160px;
    }

    img {
      display: inline-block;
      height: auto;
      max-width: 100%;
      max-height: 100%;
    }
  }

  .text {

    &-heading {
      margin-bottom: 20px;
      text-align: center;

      @include media(">=tablet") {
        display: flex;
        align-items: baseline;
        justify-content: space-between;
        text-align: left;
        margin-bottom: 30px;
      }

      h3 {
        margin-bottom: 5px;

        @include media(">=tablet") {
          margin-right: 5px;
          margin-bottom: 0;
        }

      }

      .green-link {
        margin-left: 5px;
      }

    }
  }
}