.page-heading {
  position: relative;
  padding: 45px 15px;
  text-align: center;
  background-color: $darkgray;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @include media(">=desktop"){
    padding-top: 66px;
    padding-bottom: 74px;
  }

  .breadcrumbs{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    text-align: left;
    z-index: 1;
    padding: 10px 15px;
    @include media(">=desktop"){
      padding-top: 25px;
      padding-left: 35px;
    }

    @include media(">=large-desktop") {
      padding-left: 50px;
    }
  }

  h1 {
    display: inline-block;
    position: relative;
    color: $white;

    &:after{
      content: '';
      position: absolute;
      bottom: -11px;
      left: 50%;
      transform: translateX(-50%);
      width: 50%;
      height: 5px;
      border-radius: 3px;
      background-color: $green;
    }
  }

  &.transparent {
    background-color: transparent;
    @include media(">=desktop"){
      padding-top: 100px;
      padding-bottom: 70px;
    }

    h1, .h2 {
      font-size: 30px;
      line-height: 36px;
      color: $titlegray;

      @include media(">=desktop"){
        font-size: 48px;
        line-height: 54px;
      }

      &::after {
        display: none;
      }
    }
  }
}

