.success-story {
  position: relative;
  padding: 60px 0;
  background-color: $white;
  overflow: hidden;

  @include media(">=desktop") {
    padding: 100px 0;
  }

  @include media(">=large-desktop") {
    padding: 140px 0 133px;
  }

  &.data-center {
    padding-top: 0;

    &::before {
      display: none;
    }
  }

  &.visible {

    &::before {
      transform: translate(85%, -50%) scale(1);

      @include media(">=desktop") {
        transform: translate(80%, -50%) scale(1);
      }

      @include media(">=large-desktop") {
        transform: translate(85%, -50%) scale(1);
      }
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    border-radius: 50%;
    background-color: $darkgray;
    opacity: .075;
    transform-origin: right center;
    will-change: transform;
    transition: transform 2s ease-in-out!important;
    transform: translate(85%, -50%) scale(0);
    height: 294px;
    width: 294px;
    box-shadow:
        0 0 0 50px rgba($white, 1),
        0 0 0 105px rgba($darkgray, 0.5);

    @include media(">=desktop") {
      transform: translate(85%, -50%) scale(0);
      height: 586px;
      width: 586px;
      box-shadow:
          0 0 0 95px rgba($white, 1),
          0 0 0 205px rgba($darkgray, 0.5);
    }

    @include media(">=large-desktop") {
      transform: translate(85%, -50%) scale(0);
      height: 820px;
      width: 820px;
      box-shadow:
          0 0 0 130px rgba($white, 1),
          0 0 0 280px rgba($darkgray, 0.5);
    }
  }

  .h1 {
    margin-bottom: 28px;

    @include media(">=desktop") {
      position: relative;
      margin-bottom: 45px;

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 10px;
        height: 10px;
        margin-left: 15px;
        border-radius: 50%;
        background-color: $red;
      }
    }

    @include media(">=large-desktop") {
      margin-bottom: 60px;
    }
  }

  .wrapp {

    @include media(">=desktop") {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 5px 0;
    }

    @include media(">=large-desktop") {
      padding: 8px 0;
    }
  }

  .video-wrapper {
    margin-bottom: 30px;

    @include media(">=desktop") {
      margin: 0;
      max-width: calc(50% - 15px);
      max-height: 320px;
    }

    @include media(">=large-desktop") {
      max-height: 420px;
    }
  }

  .description {
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    color: $bodygray;

    @include media(">=desktop") {
      max-width: calc(50% - 15px);
      padding-left: 100px;
      font-size: 28px;
      line-height: 38px;
    }

    @include media(">=large-desktop") {
      padding: 15px 0 0 138px;
    }

    strong {
      color: $titlegray;
    }

    p {
      margin: 0;
    }

    img {
      display: inline-block;
      max-height: 50px;
      margin-top: 30px;
    }
  }
}