.services-list {
  margin-bottom: 60px;

  @include media(">=desktop") {
    margin-bottom: 100px;
  }

  @include media(">=large-desktop") {
    margin-bottom: 140px;
  }
}

.services-card {

  margin-bottom: 40px;

  @include media(">=desktop") {
    margin-bottom: 60px;
    overflow: hidden;
  }

  @include media(">=large-desktop") {
    margin-bottom: 100px;
  }

  &:last-child {
    margin-bottom: 0;
  }

  .thumbnail {
    position: relative;
    width: 100%;
    margin-bottom: 25px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;

    @include media(">=desktop") {
      float: left;
      margin: 0 80px 0 0;
      max-width: 480px;
    }

    @include media(">=large-desktop") {
      margin: 0 160px 0 0;
      max-width: 620px;
    }

    &::before {
      content: '';
      display: block;
      padding-bottom: 56.25%;

      @include media(">=desktop") {
        padding-bottom: 100%;
      }
    }
  }

  .description {

    @include media(">=desktop") {
      padding: 15px 0;
      overflow: hidden;
    }

    @include media(">=large-desktop") {
      padding: 55px 0;
    }

    & > div{
      position: relative;
      min-height: 40px;
      margin-bottom: 15px;
      padding-left: 60px;
      overflow: hidden;

      @include media(">=large-desktop") {
        min-height: 50px;
        margin-bottom: 26px;
        padding-left: 80px;
      }
    }


    img, .lottie-icon {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      display: block;
      max-height: 50px;
      height: auto;
      width: 100%;
      max-width: 50px;

      @include media(">=large-desktop") {
        max-height: 60px;
        max-width: 60px;
      }
    }

    h3, .h3 {

      &:hover {

        a::before {
          transform: translateY(0);
        }

        a::after {
          transform: translateY(200%);
        }
      }
    }

    p {
      margin-bottom: 25px;
      color: $bodygray;

      @include media(">=large-desktop") {
        margin-bottom: 45px;
      }
    }

    a {
      color: transparent;

      &::before,
      &::after {
        display: flex;
        align-items: center;
        content: attr(data-text);
        position: absolute;
        top: 0;
        left: 60px;
        right: 0;
        height: 100%;
        color: $titlegray;
        transition: transform .3s;

        @include media(">=large-desktop") {
          left: 80px;
        }
      }

      &::before {
        transform: translateY(-200%);
      }

      &::after {
        transform: translateY(0);
      }
    }

    ul {

      @include media(">=tablet") {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
      }
    }

    li {
      position: relative;
      display: flex;
      min-height: 40px;
      width: 100%;
      margin-bottom: 30px;
      padding-left: 60px;
      color: $titlegray;
      overflow: hidden;

      @include media(">=tablet") {
        width: 45%;

        &:nth-last-of-type(1),
        &:nth-last-of-type(2) {
          margin-bottom: 0;
        }
      }

      @include media(">=desktop") {
        margin-bottom: 25px;
      }

      @include media(">=large-desktop") {
        min-height: 60px;
        margin-bottom: 30px;
        padding-left: 80px;
      }

      &:hover {

        a::before {
          transform: translateY(0);
        }

        a::after {
          transform: translateY(200%);
        }
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .h6 {
    margin-bottom: 0;
    display: flex;
    align-items: center;
  }
}