.posts-section{
  padding: 50px 0 33px;
  background-color: $white;

  @include media(">=desktop"){
    padding-top: 100px;
    padding-bottom: 85px;
  }

  @include media(">=large-desktop"){
    padding-top: 144px;
    padding-bottom: 125px;
  }

  .slick-slider{
    padding-bottom: 21px;

    @include media(">=large-desktop"){
      padding-bottom: 30px;
    }
  }

  .slick-slide {
    margin: 0 15px;

    @include media(">=tablet"){
      &:nth-child(even){
        & > div > div{
          .slider-item{
            &:after{
              background: $white url('#{$images-path}top-bg-dots.png')no-repeat 100% 0;
            }
          }
        }
      }

      &:nth-child(odd) {
        & > div > div {
          .slider-item{
            &:after {
              background: $white url('#{$images-path}bottom-bg-dots.png') no-repeat 100% 100%;
            }
          }
        }
      }
    }
  }

  .slick-list {
    margin: 0 -15px;
    padding-bottom: 25px;

    @include media(">=tablet"){
      padding-bottom: 30px;
    }

    @include media(">=desktop"){
      padding-bottom: 42px;
    }
  }

  .h3 {
    text-align: center;
    color: $titlegray;
    margin-bottom: 40px;

    @include media(">=tablet"){
      margin-bottom: 67px;
    }
  }

  .slider-wrapper {
    width: 100%;
    max-width: 490px;
    margin: 0 auto;

    @include media(">=tablet"){
      max-width: 1010px;
    }

    @include media(">=desktop"){
      max-width: none;
    }
  }

  .slick-track{
    display: flex;
    &::before,
    &::after{
      display: none;
    }
    .slick-slide{
      flex-shrink: 0;
      float: none;
      height: auto;
      display: flex;
      flex-direction: column;
      & > div{
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        margin-bottom: 2px;
        & > div{
          display: flex!important;
          flex-direction: column;
          flex-grow: 1;
        }
      }
    }
  }

  .slider-item {
    position: relative;
    text-align: left;
    height: auto;
    flex-grow: 1;
    transition: all 0.3s;
    display: flex;
    flex-direction: column;
    padding: 30px;
    min-height: 290px;

    @include media(">=tablet"){
      padding: 35px;
    }

    @include media(">=desktop"){
      padding: 40px;
      min-height: 370px;
      box-shadow: 0 0 0 rgba(0,0,0,0), 0 0 0 rgba(0,0,0,0);
    }

    @include media(">=medium-desktop"){
      padding: 49px 51px;
    }

    @include media(">=large-desktop"){
      min-height: 490px;
    }

    &:hover{
      @include media(">=desktop"){
        box-shadow: 0 0 40px rgba(0, 0, 0, 0.1), 0 20px 40px rgba(0, 0, 0, 0.1);

        &:after{
          opacity: 0;
          pointer-events: auto;
        }

        .type{
          color: $white;
        }

        .title{

          @include media(">=desktop"){
            transform: translateY(-50px);
          }

          h3{
            color: $white;
          }
        }

        .date{
          color: $white;
        }
      }
    }

    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($darkgray, .3);
      z-index: -1;
    }

    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 2px solid $cloudgray;
      transition: all 0.3s;
      z-index: 0;
      will-change: opacity;
      background: $white url('#{$images-path}right-bg-dots.png')no-repeat 100% 50%;
    }

    .post-link{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 4;

      &:after{
        content: '';
        position: absolute;
        bottom: -2px;
        right: 0;
        left: auto;
        width: 0;
        height: 4px;
        border-radius: 2px;
        background-color: $green;
        transition: width 0.3s;
        transform-style: preserve-3d;
      }

      @include media(">=desktop"){
        &:hover{
          &:after{
            right: auto;
            left: 0;
            width: 100%;
          }
        }
      }
    }

    .type {
      display: block;
      position: relative;
      color: $graphitegray;
      font-size: 18px;
      font-weight: 600;
      line-height: 22px;
      transition: all 0.3s;
      z-index: 3;
      margin-bottom: 2px;

      @include media(">=desktop"){
        margin-bottom: 7px;
      }
    }

    .title{
      position: relative;
      max-width: 390px;
      z-index: 3;
      transition: all 0.3s;
      padding: 25px 0;
      flex-grow: 1;
      display: flex;
      flex-direction: column;

      @include media(">=tablet"){
        padding-top: 35px;
      }

      @include media(">=desktop"){
        padding-top: 45px;
      }

      @include media(">=large-desktop"){
        padding-top: 94px;
      }

      .h5 {
        margin-bottom: 14px;
        transition: all 0.3s;
        @include media(">=desktop"){
          margin-bottom: 22px;
        }
      }

      .user-name{
        display: block;
        color: $darkgreen;
        font-size: 16px;
        line-height: 20px;
        font-weight: 600;
        font-style: italic;

        @include media(">=tablet"){
          font-size: 18px;
          line-height: 22px;
        }
      }
    }

    .date{
      display: block;
      margin-top: auto;
      position: relative;
      z-index: 3;
      color: $graphitegray;
      font-size: 16px;
      line-height: 20px;
      font-weight: 600;
      transition: all 0.3s;

      @include media(">=tablet"){
        font-size: 18px;
        line-height: 22px;
      }
    }
  }

