.key-feautures {
  padding: 60px 0;

  @include media(">=desktop") {
    padding: 100px 0;
  }

  @include media(">=large-desktop") {
    padding: 140px 0;
  }

  .holder {
    position: relative;
    padding: 30px 40px;
    background-color: $lightgray;
    overflow: hidden;

    @include media(">=desktop") {
      padding: 70px 90px;
    }

    @include media(">=large-desktop") {
      padding: 100px 130px;
    }

    &.visible {

      &::before {
        transform: translate(85%, -50%) scale(1);

        @include media(">=desktop") {
          transform: translate(80%, -50%) scale(1);
        }

        @include media(">=large-desktop") {
          transform: translate(85%, -50%) scale(1);
        }
      }
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      border-radius: 50%;
      background-color: $lightgray;
      opacity: .075;
      transform-origin: right center;
      will-change: transform;
      transition: transform 2s ease-in-out!important;
      transform: translate(85%, -50%) scale(0);
      height: 294px;
      width: 294px;
      box-shadow:
      0 0 0 90px rgba($darkgray, .5),
      0 0 0 160px rgba($lightgray, 1),
      0 0 0 250px rgba($darkgray, .4);

      @include media(">=desktop") {
        transform: translate(85%, -50%) scale(0);
        height: 400px;
        width: 400px;
        box-shadow:
            0 0 0 120px rgba($darkgray, .5),
            0 0 0 220px rgba($lightgray, 1),
            0 0 0 340px rgba($darkgray, .4);
      }

      @include media(">=large-desktop") {
        transform: translate(85%, -50%) scale(0);
        height: 500px;
        width: 500px;
        box-shadow:
            0 0 0 160px rgba($darkgray, .5),
            0 0 0 290px rgba($lightgray, 1),
            0 0 0 450px rgba($darkgray, .4),
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 4px;
      border-radius: 2px;
      background-color: $green;
    }
  }

  .h4 {
    margin-bottom: 25px;

    @include media(">=desktop") {
      margin-bottom: 35px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 50px;
    }
  }

  .icon {
    display: inline-block;
    height: 30px;
    width: 30px;
    margin-bottom: 5px;

    @include media(">=desktop") {
      margin-bottom: 7px;
    }

    img {
      display: block;
      max-height: 100%;
      height: auto;
      width: 100%;
      margin-bottom: 5px;

      @include media(">=desktop") {
        margin-bottom: 8px;
      }

      @include media(">=large-desktop") {
        margin-bottom: 10px;
      }
    }
  }

  .name {
    font-family: $nunito;
    font-size: 22px;
    line-height: 28px;
    font-weight: $semibold;

    @include media(">=desktop") {
      margin-bottom: 8px;
      font-size: 24px;
      line-height: 30px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 10px;
    }
  }

  .desciption {
    color: $bodygray;
  }

  ul {

    @include media(">=mobile-landscape") {
      display: flex;
      flex-wrap: wrap;
      margin: -15px -25px;
    }

    @include media(">=desktop"){
      margin: 0 -25px;
    }
  }

  li {
    margin: 0 0 25px 0;

    @include media(">=mobile-landscape") {
      width: calc(50% - 50px);
      margin: 15px 25px;
    }

    @include media(">=desktop"){
      width: calc(25% - 50px);
      margin: 0 25px;
    }

    &:last-child {
      margin-bottom: 0;

      @include media(">=mobile-landscape"){
        margin-bottom: 15px;
      }

      @include media(">=desktop"){
        margin-bottom: 0;
      }
    }
  }
}