.google-map {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  .info-window {
    position: relative;
    display: flex;
    padding: 10px 20px;
    background-color: $darkgray;

    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-image: url(#{$images-path}pattern_point-white.png);
      background-color: transparent;
      opacity: .08;
    }
  }

  .icon {
    flex-shrink: 0;
    width: 23px;
    height: 23px;
    margin-right: 10px;

    img {
      display: block;
      max-height: 100%;
      height: auto;
      width: 100%;
    }
  }

  .title {
    max-width: 200px;
    color: $white;
    font-family: $nunito;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  .gm-ui-hover-effect {
    display: none!important;
  }

  .gm-style .gm-style-iw-c {
    padding: 0;
    border-radius: 0;
    box-shadow: none;
    background-color: gray;
  }

  .gm-style .gm-style-iw-d {
    overflow: hidden!important;
  }

  .gm-style-iw-t::before,
  .gm-style-iw-t::after {
    border-top-color: $darkgray;
    border-top-width: 6px;
    background-color: $darkgray;
  }

  .gm-style-iw-d,
  .gm-style-iw {
    max-width: 100% !important;
  }
}