.article-info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: -5px -21px;
  padding: 10px 0 25px;

  @include media(">=desktop") {
    padding-bottom: 50px;
  }

  &-item {
    margin: 5px 21px;
    position: relative;
    color: $graphitegray;
    font-size: 16px;
    line-height: 20px;
    font-weight: 600;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -21px;
      width: 1px;
      height: 20px;
      background-color: $silvergray;
    }
    &:last-child {
      &:after {
        display: none;
      }
    }

    @include media(">=desktop") {
      font-size: 18px;
      line-height: 22px;
    }
  }

  span {
    color: $darkgreen;

    &.name{
      font-style: italic;
    }
  }
}