.filter {
  padding: 15px 0;
  background-color: $lightgray;

  @include media(">=desktop"){
    padding: 0;
  }

  &.center {

    @include media(">=desktop"){
      padding: 20px 0;
    }

    @include media(">=large-desktop"){
      padding: 30px 0;
    }

    .wrapp {
      justify-content: center;
    }

    .form-group {
      width: 100%;
      max-width: 100%;

      @include media(">=desktop") {
       width: 690px;
      }

      @include media(">=large-desktop") {
        width: 750px;
      }
    }
  }

  &.multiple {
    margin-top: 20px;
    padding: 15px 0;

    @include media(">=desktop"){
      margin-top: 30px;
      padding: 20px 0;
    }

    @include media(">=large-desktop"){
      padding: 29px 0;
      margin-top: 40px;
    }

    .wrapp {
      justify-content: center;
    }

    form {

      @include media(">=desktop") {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
      }

      .search-input{
        @include media(">=desktop"){
          padding-right: 20px;
        }
      }

      & > .search-submit {
        position: relative;
        display: none;
        background-color: $white;

        @include media(">=desktop") {
          display: block;
        }
      }
    }

    .form-group {
      margin: 0 auto 20px;
      padding-right: 0;

      &:last-of-type {
        margin-bottom: 0;
      }

      @include media(">=desktop") {
        max-width: 360px;
        margin: 0 30px 0 0;

        &:last-of-type {
          margin-right: 30px;
        }
      }

      .search-submit {
        display: block;

        @include media(">=desktop") {
          display: none;
        }
      }
    }
  }

  .wrapp {

    @include media(">=desktop"){
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  .filters-list {
    margin-bottom: 25px;

    @include media(">=tablet"){
      display: flex;
      justify-content: center;
    }

    @include media(">=desktop"){
      margin-right: 10px;
      margin-bottom: 0;
      flex-shrink: 0;
    }

    li {
      text-align: center;
      color: $trafficgray;
      font-size: 20px;
      font-weight: 600;
      line-height: 26px;
      margin-bottom: 5px;

      @include media(">=tablet"){
        margin: 0 7px;
      }

      @include media(">=desktop"){
        margin: 0;

        &:hover{
          color: $black;
         //a{
         //  &::after{
         //    right: auto;
         //    left: 0;
         //    width: 100%;
         //  }
         //}
        }
      }

      &.active {
        color: $black;
        a {
          &::after {
            width: 100%;
            left: 0;
            transform: none;
            right: auto;
            opacity: 1;
          }
        }
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    a {
      display: inline-block;
      color: inherit;
      position: relative;
      text-decoration: none;

      @include media(">=tablet"){
        padding: 10px 15px;
      }

      @include media(">=desktop"){
        padding: 30px 25px
      }

      @include media(">=large-desktop"){
        padding: 43px 25px 41px;
      }

      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
        height: 4px;
        border-radius: 2px;
        background-color: $green;
        opacity: 0;
        transition: all 0.3s, left 0s, right 0s;
        @include media(">=desktop"){
          opacity: 1;
          left: auto;
          right: 0;
          width: 0;
          transform: none;
        }

      }
    }
  }

  form {

    .form-group{
      width: 100%;
      max-width: 490px;
      margin: 0 auto;
      position: relative;
      @include media(">=desktop"){
        width: 365px;
        margin: 0;
      }

      @include media(">=large-desktop"){
        width: 490px;
      }
    }

    .search-input {
      padding: 0;
      width: 100%;
      height: 50px;
      padding-left: 20px;
      padding-right: 50px;
      font-family: $nunito-sans;
      font-size: 18px;
      line-height: 48px;
      color: $black;

      &:focus {
        &::placeholder {
          color: $black;
        }
      }

      &::placeholder {
        font-family: $nunito-sans;
        font-size: 18px;
        line-height: 48px;
        color: $irongray;
      }
    }

    .search-submit{
      position: absolute;
      bottom: 0;
      right: 0;
    }
  }
}