.page-description {
  padding: 30px 0;

  @include media(">=desktop") {
    padding: 60px 0;
  }

  @include media(">=large-desktop") {
    padding: 100px 0;
  }

  &.content {
    padding-bottom: 40px;

    @include media(">=desktop") {
      padding-bottom: 50px;
    }

    @include media(">=large-desktop") {
      padding-bottom: 68px;
    }

    p {
      text-align: left;
      font-size: 16px;
      line-height: 26px;
      color: $bodygray;

      @include media(">=desktop") {
        font-size: 18px;
        line-height: 28px;
      }
    }

    h4 {

      @include media(">=desktop") {
        margin-bottom: 40px;
      }

      @include media(">=large-desktop") {
        margin-bottom: 47px;
      }
    }
  }

  .wrapp {
    max-width: calc(100% - 20px);
    margin: 0 auto;
    padding: 0 15px;
    text-align: center;
    font-size: 20px;
    line-height: 28px;
    color: $bodygray;

    @include media(">=desktop") {
      max-width: 930px;
      padding: 0 30px;
      font-size: 28px;
      font-weight: 400;
      line-height: 38px;
    }

    @include media(">=large-desktop") {
      max-width: 1070px;
    }
  }

  & + .key-feautures {
    padding-top: 0;

    @include media(">=desktop") {
      padding-top: 0;
    }

    @include media(">=large-desktop") {
      padding-top: 0;
    }
  }
}