.services-item {
  position: relative;
  min-height: 230px;
  margin-bottom: 15px;
  padding: 30px 40px;
  text-align: center;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
  background-color: $white;
  overflow: hidden;
  transition: box-shadow .3s;

  @include media(">=tablet") {
    width: calc(50% - 30px);
    margin: 0 15px 30px;
  }

  @include media(">=desktop") {
    display: flex;
    flex-direction: column;
    width: calc(33.33% - 30px);
    min-height: 370px;
    padding: 60px 50px 76px;
    text-align: left;
    overflow: visible;
    &:hover {
      box-shadow: 0 0 40px rgba(0, 0, 0, 0.1),
                  0 20px 40px rgba(0, 0, 0, 0.1);

      &::after {
        transform-origin: left;
        transform: scaleX(1);
      }

    }
  }

  @include media(">=large-desktop") {
    padding: 60px 50px 73px;
  }

  &:nth-child(even) {

    &::before {
      transform: rotate(180deg);

      @include media(">=desktop") {
        bottom: auto;
        top: 0;
        transform: scaleY(-1);
      }

      @include media(">=large-desktop") {
        top: -8px;
      }
    }
  }

  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-image: url("#{$images-path}services-pattern.png");
    background-repeat: no-repeat;
    background-position: right center;

    @include media(">=desktop") {
      top: auto;
      left: auto;
      bottom: 0;
      right: 0;
      width: 245px;
      height: 175px;
      background-image: url("#{$images-path}services-desktop-pattern.png");
      background-position: left top;
      pointer-events: none;
    }

    @include media(">=large-desktop") {
      width: 346px;
      height: 173px;
      background-image: url("#{$images-path}services-ld-pattern.png");
    }
  }

  &::after {

    @include media(">=desktop") {
      position: absolute;
      content: '';
      left: 0;
      bottom: -2px;
      height: 4px;
      width: 100%;
      border-radius: 2px;
      transform-origin: right;
      transform: scaleX(0);
      transition: transform .3s;
      will-change: transform;
      background-color: $green;
    }
  }

  .post-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
  }

  .lottie-icon {
    position: relative;
    display: flex;
    justify-content: center;
    height: 60px;
    max-width: 80px;
    margin: 0 auto 20px;
    z-index: 5;

    @include media(">=desktop") {
      justify-content: flex-start;
      margin-bottom: 32px;
      margin-left: 0;
    }

    @include media(">=large-desktop") {
      margin-bottom: 30px;
    }

    img {
      max-height: 100%;
      height: auto;
      width: 100%;
    }

    svg{
      display: block;
      width: auto!important;
    }
  }

  .h4 {
    position: relative;
    margin-bottom: 20px;
    flex-grow: 1;

    @include media(">=desktop") {
      margin-bottom: 37px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 27px;
    }
  }

  .description {
    display: none;

    @include media(">=large-desktop") {
      display: block;
      margin-bottom: 40px;
    }
  }

  .green-link {
    z-index: 5;
    margin-top: auto;
    align-self: flex-start;
  }

}