.makes-different {
  position: relative;
  padding: 60px 0;
  background-color: $lightgray;
  overflow: hidden;

  @include media(">=desktop") {
    padding: 100px 0;
  }

  @include media(">=large-desktop") {
    padding: 140px 0 142px;
  }

  &.visible {

    &::before {
      transform: translate(85%, 0) scale(1);

      @include media(">=desktop") {
        transform: translate(85%, 0) scale(1);
      }

      @include media(">=large-desktop") {
        transform: translate(85%, 0) scale(1);
      }
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    border-radius: 50%;
    background-color: $lightgray;
    opacity: .075;
    transform-origin: right bottom;
    will-change: transform;
    transition: transform 2s ease-in-out!important;
    transform: translate(85%, 0) scale(0);
    height: 294px;
    width: 294px;
    box-shadow:
        0 0 0 90px rgba($darkgray, .5),
        0 0 0 160px rgba($lightgray, 1),
        0 0 0 250px rgba($darkgray, .4);

    @include media(">=desktop") {
      transform: translate(85%, 0) scale(0);
      height: 400px;
      width: 400px;
      box-shadow:
          0 0 0 120px rgba($darkgray, .5),
          0 0 0 220px rgba($lightgray, 1),
          0 0 0 340px rgba($darkgray, .4);
    }

    @include media(">=large-desktop") {
      transform: translate(85%, 0) scale(0);
      height: 500px;
      width: 500px;
      box-shadow:
          0 0 0 160px rgba($darkgray, .5),
          0 0 0 290px rgba($lightgray, 1),
          0 0 0 450px rgba($darkgray, .4);
    }
  }

  .h2 {
    margin-bottom: 35px;

    @include media(">=desktop") {
      margin-bottom: 45px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 73px;
    }
  }

  .wrapp {

    @include media(">=tablet"){
      text-align: center;
    }

    @include media(">=desktop") {
      display: flex;
      align-items: center;
    }
  }

  .button {

    @include media(">=desktop") {
      margin-left: auto;
      flex-shrink: 0;
      margin-bottom: 16px;
    }
  }
}