
@keyframes pulse {
  0% {
    transform: translate(-50%, -50%) scale(1.1);
    opacity: 0
  }

  1% {
    opacity: 1
  }

  100% {
    transform: translate(-50%, -50%) scale(1.5);
    opacity: 0
  }
}

@keyframes scroll-pulse {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0
  }

  1% {
    opacity: 1
  }

  100% {
    transform: translate(-50%, -50%) scale(1.3);
    opacity: 0
  }
}