
.main {
  padding-top: 70px;
  flex-shrink: 0;
  flex-grow: 1;
  background-color: $white;

  @include media(">=desktop") {
    padding-top: 117px;
  }

  @include media(">=large-desktop"){
    padding-top: 121px;
  }
}

.wrapper {
  position: relative;
  overflow-x: hidden;
  width: 100%;
  @include flex(column);
  flex-shrink: 0;
  flex-grow: 1;
  min-height: 100vh;
  height: 100%;
}

.header,
.footer {
  flex-shrink: 0;
}

