.logos {
  padding: 20px 0;
  background-color: #24282c;

  @include media(">=desktop") {
    padding: 38px 0;
  }

  @include media(">=large-desktop") {
    padding: 48px 0;
  }

  .container {
    position: relative;
  }

  .slider {

    @include media(">=desktop") {
      margin: 0 auto;
      width: calc(100% - 200px);
    }

    &-arrow {
      position: absolute;
      display: none;
      top: 50%;
      transform: translateY(-50%);

      @include media(">=desktop") {
        display: block;
      }

      &-right {
        right: 30px;
      }
    }
  }

  .image {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    width: 115px;
    height: 40px;

    @include media(">=desktop") {
      width: 150px;
      height: 70px;
    }

    img {
      max-height: 100%;
      height: auto;
      width: 100%;

      @supports (padding-top: 1px){
        filter: grayscale(90%) contrast(30%);
        transition: filter .3s;
      }


      &:hover {
        @supports (padding-top: 1px){
          filter: grayscale(0%) contrast(100%);
        }
      }
    }
  }
}