.share{
  border-top: 2px solid $cloudgray;
  padding-top: 30px;
  margin-top: 30px;
  @include media(">=mobile-landscape"){
    display: flex;
    align-items: center
  }

  @include media(">=desktop"){
    margin-top: 50px;
  }

  @include media(">=large-desktop"){
    margin-top: 70px;
  }


  & > span{
    display: block;
    margin-bottom: 5px;
    color: $graphitegray;
    font-size: 20px;
    font-weight: 600;
    line-height: 26px;
    @include media(">=mobile-landscape"){
      margin-bottom: 0;
      margin-right: 30px;
    }
  }

  .share-social{
    padding: 0;
    width: 200px;
    display: flex;
    margin: 0 -10px 0;
    li{
      padding: 0;
    }
  }

  li{
    margin: 0 10px;
    &:before{
      display: none;
    }

    a{
      display: block;
      position: relative;
      overflow: hidden;
      width: 30px;
      height: 30px;
      &:before, &:after{
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        -webkit-transition: .35s;
        transition: .35s;
        background-repeat: no-repeat;
        background-position: center;
        will-change: transform;
      }
      &:before{
        transform: translateY(0);
      }
      &:after{
        transform: translateY(200%);
      }
      &:hover{
        &:before{
          transform: translateY(-200%);
        }
        &:after{
          transform: translateY(0);
        }
      }
      &.icon-facebook{
        &:before, &:after{
          background-image: url('#{$images-path}facebook-green.svg');
        }
      }
      &.icon-twitter{
        &:before, &:after{
          background-image: url('#{$images-path}twitter-green.svg');
        }
      }
      &.icon-email{
        &:before, &:after{
          background-image: url('#{$images-path}email-green.svg');
        }
      }
      &.icon-print{
        &:before, &:after{
          background-image: url('#{$images-path}print-green.svg');
        }
      }
    }
  }
}