.different {
  padding: 62px 0 60px;
  text-align: center;

  @include media(">=desktop") {
    padding: 100px 0;
  }

  @include media(">=large-desktop") {
    padding: 120px 0 135px;
  }

  .h1 {
    display: inline-block;
    position: relative;
    margin-bottom: 30px;
    color: $white;

    @include media(">=desktop") {
      margin-bottom: 48px;
      padding: 0 30px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 65px;
    }

    &::before,
    &::after {

      @include media(">=desktop") {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        height: 10px;
        width: 10px;
        border-radius: 50%;
        background-color: $red;
      }
    }

    &::before {
      left: 0;
    }

    &::after {
      right: 0;
    }
  }
}