.logos-list {
  padding-top: 8px;
  padding-bottom: 25px;

  @include media(">=desktop"){
    padding-bottom: 40px;
  }

  @include media(">=large-desktop"){
    padding-bottom: 70px;
  }

  .content-holder &{
    padding-top: 25px;

    @include media(">=tablet"){
      padding-bottom: 55px;
    }

    @include media(">=desktop"){
      padding: 35px 0 75px;
    }

    @include media(">=large-desktop"){
      padding: 38px 0 56px;
    }

    ul{
      @include media(">=desktop"){
        justify-content: center;
      }
    }

    li{

      @include media(">=large-desktop"){
        width: calc(25% - 50px);
        margin: 0 25px 50px;
      }

    }

  }

  .item {
    position: relative;
    max-width: 1270px;
    padding-bottom: 35px;
    margin: 0 auto 35px;

    @include media(">=desktop"){
      padding-bottom: 50px;
      margin-bottom: 50px;
    }

    @include media(">=large-desktop"){
      padding-bottom: 72px;
      margin-bottom: 77px;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      height: 2px;
      width: 100%;
      background-color: $cloudgray;
    }

    &:last-child {
      margin-bottom: 0;
      &::after {
        display: none;
      }
    }
  }

  .h2 {
    text-align: center;
    margin-bottom: 35px;

    @include media(">=desktop"){
      margin-bottom: 48px;
    }

    @include media(">=large-desktop"){
      margin-bottom: 62px;
    }
  }

  ul{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 -10px;

    @include media(">=tablet"){
      margin: 0 -20px -40;
    }

    @include media(">=desktop"){
      justify-content: flex-start;
      margin: 0 -25px -50px;
    }

    @include media(">=large-desktop"){
      margin: 0 -34px -50px;

    }

  }

  li{
    width: calc(100% - 20px);
    height: 45px;
    margin: 0 10px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 200px;

    @include media(">=tablet"){
      width: calc(50% - 40px);
      margin: 0 20px 40px;
    }

    @include media(">=desktop"){
      height: 65px;
      max-width: none;
      width: calc(25% - 50px);
      margin: 0 25px 50px;
    }

    @include media(">=large-desktop"){
      height: 80px;
      width: calc(20% - 68px);
      margin: 0 34px 50px;
    }

    img{
      display: inline-block;
      min-width: 1%;
      flex-shrink: 0;
      max-width: 100%;
      max-height: 100%;

      //@include media(">=desktop"){
      //  max-height: 70%;
      //}
    }
  }
}