.contacts-section {
  position: relative;
  padding: 50px 15px;
  text-align: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  @include media(">=tablet") {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @include media(">=desktop") {
    padding-top: 85px;
    padding-bottom: 70px;
  }

  @include media(">=large-desktop") {
    padding-top: 103px;
    padding-bottom: 95px;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($darkgray, .5);
    z-index: 0;
  }

  .holder {
    position: relative;
    z-index: 1;
    max-width: 1010px;
    margin: 0 auto;

    @include media(">=tablet"){
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  h4 {
    color: $white;
    margin-bottom: 26px;
  }

  &-description {
    width: 100%;
    color: $white;
    margin-bottom: 30px;

    @include media(">=tablet"){
      margin-bottom: 42px;
    }
  }

  .links-block {
    max-width: 437px;
    margin: 0 auto;

    @include media(">=tablet") {
      display: flex;
      align-items: flex-start;
      justify-content: center;
      margin: 0 -15px;
      max-width: 467px;
    }

    @include media(">=desktop"){
      margin: 0 -39px;
      max-width: 515px;
    }

    & > a {
      margin-bottom: 20px;

      @include media(">=tablet") {
        margin: 10px 15px 0;
      }

      @include media(">=desktop"){
        margin: 10px 39px 0;
      }

    }

    .call-block {

      @include media(">=tablet"){
        margin: 0 15px;
      }

      @include media(">=desktop"){
        margin: 0 39px;
      }

      span {
        display: block;
        max-width: 189px;
        color: $silvergray;
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
        margin: 0 auto 7px;
      }
    }
  }

  & + .page-description {
    padding-top: 30px;

    @include media(">=desktop") {
      padding-top: 45px;
    }

    @include media(">=large-desktop") {
      padding-top: 65px;
    }
  }
}