.result-list {
  padding: 20px 0 10px;

  @include media(">=large-desktop"){
    padding: 24px 0 10px;
  }

  &.search {
    padding-top: 20px;

    @include media(">=desktop"){
      padding-top: 35px;
    }

    @include media(">=large-desktop"){
      padding-top: 50px;
    }
  }

  .wrapp {
    max-width: calc(100% - 20px);
    margin: 0 auto;
    padding: 0 15px;

    @include media(">=desktop") {
      max-width: 930px;
      padding: 0 30px;
    }

    @include media(">=large-desktop") {
      max-width: 1070px;
    }
  }

  .result-total {
    color: $graphitegray;
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;

    @include media(">=desktop") {
      font-size: 20px;
      line-height: 24px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 16px;
    }

    strong {
      color: $titlegray;
    }
  }

  .item {
    padding: 25px 0;
    border-bottom: 2px solid $cloudgray;
    color: $bodygray;

    &:last-of-type {
      border: none;
    }

    @include media(">=desktop") {
      padding: 35px 0;
    }

    @include media(">=large-desktop") {
      padding: 52px 0 51px;
    }
  }

  .h4 {
    margin-bottom: 0;
  }

  .category {
    margin-bottom: 10px;
    color: $graphitegray;
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;

    @include media(">=desktop") {
      margin-bottom: 18px;
      font-size: 20px;
      line-height: 26px;
    }

    a {
      color: inherit;
    }
  }

  .green-link {
    display: inline-block;
    margin-top: 15px;

    @include media(">=desktop") {
      margin-top: 25px;
    }

    @include media(">=large-desktop") {
      margin-top: 30px;
    }
  }
}