.single-map {
  padding: 40px 0;
  text-align: center;

  @include media(">=desktop") {
    padding: 70px 0;
  }

  @include media(">=large-desktop") {
    padding: 100px 0;
  }

  .google-map {
    position: relative;
    height: 340px;

    @include media(">=desktop") {
      height: 440px;
    }

    @include media(">=large-desktop") {
      height: 540px;
    }
  }

  .h4 {
    margin-bottom: 25px;

    @include media(">=desktop") {
      margin-bottom: 40px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 47px;
    }
  }
}