.footer {
  position: relative;
  color: $trafficgray;

  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-image: url("../images/pattern_point-white.png");
    background-color: transparent;
    opacity: .08;
  }

  a {
    color: inherit;
    text-decoration: none;
    font-size: inherit;
    line-height: inherit;

    &:hover {
      text-decoration: underline;
      color: inherit;
    }
  }

  .navigation {
    padding: 130px 40px 40px 40px;
    background-color: $darkgray;

    @include media(">=desktop") {
      padding: 60px 55px 60px;
    }

    @include media(">=large-desktop") {
      padding: 80px 195px;
    }

    [class^="col"] {
      margin-bottom: 38px;

      &:last-of-type {
        margin-bottom: 0;
      }

      @include media(">=desktop") {
        margin-bottom: 0;
      }
    }

    h5, h6 {
      position: relative;
      margin-bottom: 18px;
      z-index: 1;
      color: $white;
      font-family: $nunito-sans;
      font-size: 16px;
      font-weight: 600;
      line-height: 20px;

      @include media(">=desktop") {
        margin-bottom: 24px;
        font-size: 18px;
        line-height: 22px;
      }

      @include media(">=large-desktop") {
        margin-bottom: 30px;
      }
    }

    ul {
      position: relative;
      max-width: 170px;
      z-index: 1;

      @include media(">=large-desktop") {
        max-width: 100%;
      }
    }

    li {
      margin-bottom: 16px;
      font-size: 16px;
      line-height: 20px;

      @include media(">=desktop") {
        margin-bottom: 15px;
      }

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  .bottom {
    padding: 65px 40px;
    background-color: #1f2428;

    @include media(">=desktop") {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: row-reverse;
      padding: 28px 55px;
    }

    @include media(">=large-desktop") {
      padding: 28px 195px;
    }
  }

  .socials {
    position: relative;
    margin-bottom: 44px;
    z-index: 1;

    @include media(">=desktop") {
      display: flex;
      margin: 0 78px 0 0;
    }

    @include media(">=large-desktop") {
      margin: 0 55px 0 0;
    }

    p {
      margin-bottom: 18px;
      color: $white;
      font-weight: 600;

      @include media(">=desktop") {
        margin: 0 40px 0 0;
      }
    }

    ul {
      display: flex;
    }

    li {
      position: relative;
      margin-right: 30px;
      height: 21px;
      width: 21px;
      &:last-child {
        margin-right: 0;
      }
    }

    a {
      display: inline-block;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      overflow: hidden;

      span {
        display: block;
        position: absolute;
        content: '';
        bottom: 0;
        left: 0;
        height: 100%;
        width: 100%;
        transition: .35s;
        background-size: 21px 21px;
        background-repeat: no-repeat;
        background-position: center bottom;
        will-change: transform;
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        transform: translateY(-200%);
        max-height: 100%;
        height: 21px;
        width: 21px;
      }

      &:hover {

        span {
          transform: translateY(200%);
        }
      }
    }
  }

  .info {
    position: relative;
    z-index: 1;
    font-size: 16px;
    line-height: 20px;

    @include media(">=desktop") {
      display: flex;
      flex-direction: column;
    }

    ul {
      margin-bottom: 45px;

      @include media(">=desktop") {
        margin-bottom: 13px;
      }

      li {
        margin: 0 0 13px 0;

        @include media(">=desktop") {
          display: inline-block;
          margin: 0 18px 0 0;
          padding-right: 22px;
          border-right: 1px solid $graphitegray;
        }

        &:last-child {
          margin: 0;

          @include media(">=desktop") {
            margin-right: 0;
            padding-right: 0;
            border: none;
          }
        }
      }
    }
  }

  .to-top {
    position: absolute;
    top: 40px;
    left: 40px;
    height: 55px;
    width: 55px;
    min-width: 55px;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;

    @include media(">=desktop") {
      position: relative;
      top: 0;
      left: 0;
      margin-left: 60px;
    }

    @include media(">=large-desktop") {
      margin-left: 55px;
    }

    svg {
      position: absolute;
      top: 0;
      left: 0;
      max-height: 100%;
      height: auto;
      width: 100%;
      overflow: visible;

      .border{
        fill: transparent;
        stroke: $green;
        stroke-width: 4;
        stroke-dasharray: 200 200;
        stroke-dashoffset: -120;
      }
    }

    &:hover {
      svg {
        .border {
          @include media(">=desktop"){
            stroke: $green;
            transition: all .35s ease;
            stroke-dashoffset: 0;
          }
        }
      }
    }
  }
}