.solutions {
  position: relative;
  padding: 60px 0 95px;
  background-color: $darkgray;

  @include media(">=desktop") {
    padding: 100px 0 160px;
  }

  @include media(">=large-desktop") {
    padding: 140px 0 250px;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-image: url("#{$images-path}pattern_point-white.png");
    opacity: .08;
  }

  .container {
    position: relative;
    z-index: 5;

    @include media(">=desktop") {
      max-width: 100%;
      position: relative;
      left: 50%;
      transform: translateX(-640px);
      padding: 0 0 0 55px;
      margin-right: 0;
    }

    @include media(">=large-desktop") {
      transform: translateX(-920px);
      padding: 0 0 0 155px;
    }
  }

  .h1 {
    position: relative;
    margin-bottom: 28px;
    color: $white;

    @include media(">=desktop") {
      margin-bottom: 41px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 70px;
    }

    &::after {

      @include media(">=desktop") {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        height: 10px;
        width: 10px;
        margin-left: 15px;
        border-radius: 50%;
        background-color: $red;
      }
    }
  }

  .cards {
    overflow-x: auto;
    white-space: nowrap;
    touch-action: manipulation;
    -ms-touch-action: manipulation;
  }

  .solutions-card {
    position: relative;
    display: inline-flex;
    flex-shrink: 0;
    width: 290px;
    margin-right: 30px;
    margin-bottom: 80px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    white-space: normal;

    @include media(">=desktop") {
      width: 370px;
      margin-bottom: 96px;

      &::before {
        height: 200px;
      }
    }

    @include media(">=large-desktop") {
      width: 470px;
      margin-bottom: 105px;
      margin-right: 60px;
    }

    &:last-child {
      margin-right: 0;

      @include media(">=desktop"){
        margin-right: 55px;
      }

      @include media(">=large-desktop"){
        margin-right: 215px;
      }

      @include media(">=ultra-desktop"){
        margin-right: 855px;
      }

    }

    &.pseudo-hover {
      .holder {
        background-color: $white;
        opacity: 1;
        pointer-events: auto;

        &::before {
          transform: scaleX(1);
        }
      }

      & > .h4,
      &::before {
        opacity: 0;
        pointer-events: none;
      }

      .h4 {
        color: $titlegray;
      }

      .description {
        visibility: visible;
      }
    }

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      height: 150px;
      width: 100%;
      background-image: linear-gradient(180deg, rgba(24, 29, 33, 0) 0%, #181d21 100%);
    }

    & > .h4 {
      color: $white;
      position: absolute;
      bottom: 40px;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      padding: 0 15px;
      text-align: center;

      @include media(">=desktop") {
        bottom: 57px;
      }

      a {
        color: inherit;
        transition: color .3s;
      }
    }

    .holder {
      display: flex;
      flex-direction: column;
      min-height: 370px;
      width: 100%;
      padding: 30px 40px;
      transition: .3s;
      opacity: 0;
      pointer-events: none;

      @include media(">=desktop") {
        min-height: 472px;
        padding: 55px 30px 50px 37px;
      }

      @include media(">=large-desktop") {
        min-height: 600px;
        padding: 138px 50px 137px 50px;
      }

      &::before {
        content: '';
        position: absolute;
        bottom: -2px;
        left: 0;
        width: 100%;
        height: 4px;
        border-radius: 2px;
        background-color: $green;
        transform-origin: left;
        transform: scaleX(0);
        transition-delay: .3s;
        transition-duration: .3s;
      }

      .h4 {
        text-align: left;
        flex-shrink: 0;

        @include media(">=desktop") {
          margin-bottom: 17px;
        }

        @include media(">=large-desktop") {
          margin-bottom: 27px;
        }

      }
    }

    .description {
      visibility: hidden;
      flex-shrink: 0;
      margin-bottom: 25px;

      @include media(">=desktop") {
        display: flex;
        color: $bodygray;
        overflow: hidden;
        margin-bottom: 31px;
      }

      @include media(">=large-desktop") {
        margin-bottom: 40px;
      }
    }

    .green-link {
      align-self: flex-start;
      display: inline-flex;
      text-decoration: none;
      margin-top: auto;

      &:before, &::after {
        top: 11px;
      }
    }
  }

  .mCS-dark-3.mCSB_scrollTools .mCSB_draggerRail {
    position: relative;
    background-color: transparent;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      border-bottom: 2px dashed #6e6e6e;
    }
  }

  .mCSB_scrollTools {
    opacity: 1;
  }

  .mCSB_scrollTools.mCSB_scrollTools_horizontal {
    height: 80px;
  }

  .mCSB_draggerContainer {

    @include media(">=desktop") {
      max-width: 1170px;
    }

    @include media(">=large-desktop") {
      max-width: 1530px;
    }
  }

  .mCSB_dragger_bar {
    height: 46px !important;
    background-color: $green !important;
    background-image: url("#{$images-path}scrollbar-toggler-ico.png");
    background-repeat: no-repeat;
    background-position: center center;

    @include media(">=desktop") {
      height: 54px !important;
    }

    &:hover {
      background-color: $green !important;

      &::after {
        opacity: 1;
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 60px;
      height: 60px;
      border-radius: 50%;
      border: 2px solid $white;
      opacity: .3;
      transition: all 0.3s;

      @include media(">=desktop") {
        width: 70px;
        height: 70px;
      }
    }
  }

  .mCSB_draggerRail {
    top: 19px;
    height: auto;

    @include media(">=desktop") {
      top: 22px;
    }
  }

  .mCSB_dragger {
    min-width: 46px;
    width: 46px !important;
    border-radius: 50% !important;

    @include media(">=desktop") {
      min-width: 54px;
      width: 54px !important;
    }
  }

  .mCS-dark-3.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
    border-radius: 50% !important;

  }

  .mCSB_horizontal.mCSB_outside > .mCSB_container {
    display: flex;
  }
}