.posts-list{
  padding: 40px 0;
  &.filtered{
    .article-info-item{
      &:first-child{
        display: none;
      }
    }
  }

  @include media(">=desktop"){
    padding: 65px 0 50px;
    .posts-wrapper{
      display: flex;
      flex-wrap: wrap;
      margin: 0 -15px;
    }
  }

  @include media(">=large-desktop"){
    padding-top: 100px;
    padding-bottom: 140px;
    .posts-wrapper{
      margin: 0 -30px;
    }
  }

  .list-item{
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 735px;
    margin: 0 auto 40px;

    @include media(">=desktop"){
      width: calc(50% - 30px);
      margin: 0 15px 55px;

      &:hover{
        .image{
          box-shadow: 0 0 40px rgba(0, 0, 0, 0.1),
          0 20px 40px rgba(0, 0, 0, 0.1);

          &::after{
            left: 0;
            right: auto;
            width: 100%;
          }
        }
      }
    }

    @include media(">=large-desktop"){
      width: calc(50% - 60px);
      margin: 0 30px 67px;
    }
  }

  .image{
    position: relative;
    width: 100%;
    padding-top: 54.421%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 158px;

    @include media(">=576px"){
      min-height: 297px;
    }

    @include media(">=tablet"){
      min-height: 400px;
    }

    @include media(">=desktop"){
      box-shadow: 0 0 0 rgba(0, 0, 0, 0.1),
      0 0 0 rgba(0, 0, 0, 0.1);
      transition: box-shadow 0.3s;
      min-height: 310px;
    }

    @include media(">=large-desktop"){
      min-height: 400px;
    }

    &::after{
      content: '';
      position: absolute;
      bottom: -2px;
      left: auto;
      right: 0;
      width: 0;
      height: 4px;
      border-radius: 2px;
      background-color: $green;
      transition: width 0.3s;
    }
  }

  .post-link{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }

  .article-info{
    margin-top: 0;
    padding-top: 15px;
    padding-bottom: 15px;

    @include media(">=tablet"){
      padding-top: 36px;
      padding-bottom: 22px;
    }
  }

  .h4{
    padding-right: 5px;
  }

  .excerpt{
    color: $bodygray;
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;

    @include media(">=desktop"){
      font-size: 18px;
      line-height: 28px;
    }

  }
}