.top-screen {
  position: relative;
  margin: -70px 0 0;
  background-color: $darkgray;

  @include media(">=desktop") {
    margin: -117px 0 0;
  }

  @include media(">=large-desktop") {
    margin: -125px 0 0 0;
  }

  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-image: url("#{$images-path}pattern_point-white.png");
    opacity: .08;
    cursor: pointer;
    pointer-events: none;
    z-index: 10;
  }
}

.posts-section {

  .home & {

    .posts-header {
      margin-bottom: 28px;

      @include media(">=desktop") {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 12px;
      }

      @include media(">=large-desktop") {
        margin-bottom: 35px;
      }

      .h1 {
        margin-bottom: 30px;

        @include media(">=desktop") {
          position: relative;
          padding-right: 25px;

          &::before {
            content: '';
            position: absolute;
            top: 50%;
            right: 0;
            width: 10px;
            height: 10px;
            background-color: $red;
            border-radius: 50%;
          }
        }
      }
    }
  }
}