.person-article {
  position: relative;
  padding: 20px 0 60px;

  @include media(">=desktop") {
    padding: 30px 0 100px;
  }

  @include media(">=large-desktop") {
    padding: 40px 0 140px;
  }

  &.visible {

    &::before {
      transform: translate(105%, -20%) scale(1);

      @include media(">=desktop") {
        transform: translate(105%, -20%) scale(1);
      }

      @include media(">=large-desktop") {
        transform: translate(105%, -20%) scale(1);
      }
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    border-radius: 50%;
    background-color: $lightgray;
    opacity: .075;
    transform-origin: right bottom;
    will-change: transform;
    transition: transform 2s ease-in-out!important;
    transform: translate(105%, -20%) scale(0);
    height: 294px;
    width: 294px;
    box-shadow:
        0 0 0 90px rgba($darkgray, .5),
        0 0 0 160px rgba($lightgray, 1),
        0 0 0 250px rgba($darkgray, .4);

    @include media(">=desktop") {
      transform: translate(105%, -20%) scale(0);
      height: 400px;
      width: 400px;
      box-shadow:
          0 0 0 120px rgba($darkgray, .5),
          0 0 0 220px rgba($lightgray, 1),
          0 0 0 340px rgba($darkgray, .4);
    }

    @include media(">=large-desktop") {
      transform: translate(105%, -20%) scale(0);
      height: 500px;
      width: 500px;
      box-shadow:
          0 0 0 160px rgba($darkgray, .5),
          0 0 0 290px rgba($lightgray, 1),
          0 0 0 450px rgba($darkgray, .4);
    }
  }

  .wrapp {
    position: relative;
    display: flex;
    flex-direction: column-reverse;
    max-width: 100%;
    margin: 0 auto;
    padding: 0 15px;
    z-index: 5;

    @include media(">=desktop") {
      flex-direction: row;
      align-items: flex-start;
      max-width: 1030px;
      padding: 0 30px;
    }

    @include media(">=large-desktop") {
      max-width: 1330px;
    }
  }

  .description {

    @include media(">=desktop") {
      flex-grow: 1;
    }
  }

  .h2 {
    margin-bottom: 8px;

    @include media(">=large-desktop") {
      margin-bottom: 13px;
    }
  }

  .position {
    margin-bottom: 20px;

    @include media(">=desktop") {
      margin-bottom: 25px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 48px;
    }
  }

  .about {
    margin-bottom: 20px;
    color: $bodygray;
    font-size: 22px;
    line-height: 28px;

    @include media(">=desktop") {
      font-size: 28px;
      line-height: 38px;
    }

    @include media(">=large-desktop") {
      margin-bottom: 30px;
    }
  }

  p {
    margin-bottom: 20px;

    &:last-of-type {
      margin-bottom: 0;
    }

    @include media(">=large-desktop") {
      margin-bottom: 30px;
    }
  }

  .photo {
    margin: 0 auto 45px;
    width: 100%;
    max-width: 490px;

    @include media(">=desktop") {
      width: 380px;
      min-width: 380px;
      margin-bottom: 0;
      margin-left: 80px;
    }

    @include media(">=large-desktop") {
      width: 490px;
      min-width: 490px;
      margin-left: 170px;
    }

    img {
      display: block;
      max-height: 100%;
      height: auto;
      width: 100%;
    }
  }

  .position {
    color: $graphitegray;
    font-family: $nunito-sans;
    font-size: 20px;
    line-height: 26px;
  }
}